import {api} from '/js/api'
import {request} from "/js/util";
import {Languages} from "../../js/util";


const typeId = request['id'];

const pageSize = 1000;

let pageIndex = 0;
let pageCount = 0;
let totalRecord = 0;

let newsList = $('#newsList');
let pullRef;
let render = template.compile(document.getElementById('tpl-atom').innerHTML);


api({
	getList: `mediaexhibtion/QueryAllEnableMediaExhibtionInfos?type=1&mediaExhitionInfoTypeId=${typeId}&pageSize=${pageSize}`
});




/**
 * 下拉刷新具体业务实现
 */
function pulldownRefresh() {
	api.getList({pageIndex: 0}, data => {
		pageIndex = 0;
		pageCount = Math.ceil(data.totalRecord / pageSize);
		totalRecord = data.totalRecord;

		data.readMore = Languages.package.readMore;
		newsList.html(render(data));
		pullRef.endPulldownToRefresh(); //refresh completed
	});
}


/**
 * 上拉加载具体业务实现
 */
function pullupRefresh() {
	api.getList({pageIndex}, data => {
		if (pageIndex === 0) {
			pageCount = Math.ceil(data.totalRecord / pageSize);
			totalRecord = data.totalRecord;
		}
		pageIndex++;


		data.readMore = Languages.package.readMore;
		newsList.append(render(data));
		pullRef.endPullupToRefresh(pageIndex + 1 >= pageCount); //refresh completed
	});

}

mui.init({
	pullRefresh: {
		container: '#pullrefresh',
		down: {
			callback: pulldownRefresh
		},
		up: {
			contentdown: Languages.package.contentdown,
			contentinit: Languages.package.contentinit,
			contentrefresh: Languages.package.contentrefresh,
			contentnomore: Languages.package.contentnomore,
			callback: pullupRefresh
		}
	}
});


if (mui.os.plus) {
	mui.plusReady(function () {
		setTimeout(function () {
			pullRef = mui('#pullrefresh').pullRefresh();
			pullRef.pullupLoading();
		}, 1000);

	});
} else {
	mui.ready(function () {
		pullRef = mui('#pullrefresh').pullRefresh();
		pullRef.pullupLoading();
	});
}
